<style lang="scss" scoped>
.login{
    background-color: #304156;
    width: 100%;
    padding: 120px 40px 0 0;
    text-align: left;
    .title{
        color: white;
        text-align: center;
        margin-bottom: 50px;
    }
    .login_form{
        // padding: 200px 0 0 0;
        .form_item{
            .form_item_input{
                width: 250px;
            }
            .label_text{
                color: white;
            }
        }
    }
}
</style>
<style scope>
/* .el-form-item__label{
    font-size: 16px;
    color: white ;
    font-weight: bold;
    padding-right: 40px
} */
</style>
<template>
    <div class="item_column login">
        <h1 class="title">
                系统登录
        </h1>
        <div class="item_row login_box">
            <div>
                <img src="@/assets/image/index/index-red.png" width="400px"/>
            </div>
            <el-form class="login_form" label-width="120px">
                <el-form-item class="form_item">
                    <label slot="label" class="label_text">账号</label>
                    <el-input v-model="user.username" class="form_item_input" size="small"></el-input>
                </el-form-item>
                <el-form-item class="form_item">
                    <label slot="label" class="label_text">密码</label>
                    <el-input type="password" v-model="user.password" class="form_item_input" size="small"></el-input>
                </el-form-item>
                <!-- <el-form-item label="记住我">
                    <el-switch active-color="#13ce66" v-model="user.remeberMe"></el-switch>
                </el-form-item>         -->
                <el-form-item>
                    <el-button type="primary" @click="login" size="small">登录</el-button>
                    <!-- <el-button size="small">注册</el-button> -->
                    <el-button type="info" @click="back" size="small">返回</el-button>
                </el-form-item>
            </el-form>
        </div>
  </div>
</template>
<script>
export default {
    name: 'login',
    data(){
        return{
            user: {
                username:'admin',
                password:'admin',
                // rememberMe: 0,
            },
            userTmp: {
                id: '1',
                username: 'admin',
                password: 'admin',
                email: '523179414@qq.com',
                phone: '187888899991',
                sex: '1',
                age: '22',
                status: '1',
                create_time: '2018-05-23 21:22:06',
                last_login_time: '2019-11-15 16:07:37',
            },
            accessToken:'J5D8F3E8D2S9DF288SWV'
            
        }
    },
    methods:{
        login(){
             post('/pms_api/login',this.user).then(data => {
                if(data.status==200){
                    this.$getGlobal.setItem('accessToken',this.accessToken)
                    this.$router.push({
                        path: '/workdest'
                    })
                }
                
            }).catch(err => {
                console.log('err',err)
            })
        },
        back(){
            this.$router.back(-1)
        }
    }
}
</script>




